.google {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: solid 4px #4885ed;
    background-color: #4885ed;
    width: max-content;
    margin: 10px auto 0 auto;
    &:hover {
      cursor: pointer;
      box-shadow: 0px 0px 10px 3px #a9c3ec;
    }
    img {
      height: 40px;
      margin-right: 10px;
    }
    button {
      background-color: transparent;
      border: none;
      color: white;
      font-size: 1rem;
      padding-right: 10px;
      &:hover {
        cursor: pointer;
      }
    }
  }